.navigation {
  display: flex;
  justify-content: space-between;
}

.navigation__list {
  margin: 0;
  display: flex;
  list-style-type: none;
  flex-wrap: wrap;
  padding: 0;

  @media (max-width: 767px) {
    margin-top: 55px;
  }
}

.navigation__item + .navigation__item {
  margin-left: 33px;

  @media (max-width: 1023px) {
    margin-left: 24px;
  }

  @media (max-width: 767px) {
    margin-left: 0;
    margin-top: 8px;
  }
}

.navigation__item a {
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  align-items: center;
  color: #f9fbfd;
  padding: 10px 0 10px 0;

  @media (max-width: 1023px) {
    font-size: 14px;
    line-height: 17px;
  }

  @media (max-width: 767px) {
    padding: 10px;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    display: flex;
    align-items: center;
    color: #011c40;

    &:hover {
      border: none;
      box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    }
  }

  &:hover {
    border-bottom: 2px solid rgb(0, 119, 255);
  }
}
